.pagetwo {
    padding-left: 10px;
    width: 100%;
}

.pagetwo_head {
    margin-top: 40px;

    width: auto;
    border-radius: 10px;
}

.pagetwo_head header {
    padding-top: 20px;
    color: #203562;
    font-weight: bolder;
}

.wrapped {
    height: auto;
    border-radius: 10px;
    background-color: white;
    padding-left: 20px;

}


.pagetwo_notes textarea {
    border: 1px;
    border-radius: 10px;
    margin-top: 30px;
    margin-bottom: 10px;
}

.pagetwo_notesfooter button {
    margin-bottom: 20px;
}

.pagetwo_notestore {

    max-height: 225px;
    padding: 5px 10px;
    border-radius: 10px;
    border: 1px solid #203562;
    overflow-x: auto;
}

.pagetwo_notestore textarea {
    margin-right: 20px;
    box-shadow: 1px 5px 5px 3px rgba(154, 154, 154, 0.333);
    border: 1px;
    border-radius: 10px;
    margin-top: 10px;
}

.pagetwo_footer h4 {
    color: #203562;
    max-width: fit-content;
    margin-top: 50px;
    margin-bottom: 2px;
}

.text_icons {
    margin-bottom: 10px;
    width: 350px;
    display: flex;
    justify-content: space-around;
}

.text_icons svg:hover {
    cursor: pointer;
    color: #203562;

}



.pagetwo_footer p {
    font-weight: 299;
    font-size: smaller;
}