.sidebar {
    margin: 0;
    padding: 0;

    width: 250px;
    background-color: #F5F5F5;
    display: grid;
    place-items: center;
    color: #203562;
    font-weight: 500;

}


.navtop {

    display: flex;
    align-items: center;
    font-size: small;
    padding-right: 15px;
}

.dp {
    margin: 20px;
    margin-top: 10px;
    margin-right: 10px;
    font-size: 35px;
    /* background-color: #203562;
    color: white; */
    border-radius: 100%;
    border-color: #203562;
    min-width: 50px;
    padding-left: 10px;
}

.dp>img {
    width: 100%;
    height: 50px;
    border: 3px solid #14d8e6;
    border-radius: 200px;
}

.navmid {
    margin: 0;
    line-height: 40px;



}

.navmid>ul {
    padding-right: 27px;

}

.navmid>ul>li {
    
    cursor: pointer;
    width: 200px;


}

li:hover {
    background-color: #203562;
    transition-duration: 0.2s;
    color: #F5F5F5;
    border-radius: 10px;
    font-size: 20px;
}





.navmid>ul>li>svg {


    margin-right: 10px;
    margin-bottom: 2px;

}

.sidebar_footer {
   max-height: fit-content;
    width: 90%;
    display: grid;
    text-align: center;
}